










import Vue from 'vue'

export default Vue.extend({
  name: 'category-box',
  props: {
    category: {
      type: Object
    }
  },
  methods: {
    goToCategory() {
      const transformedName = this.category.cat_name.replace(/\s/g, '+')

      this.$router.push({
        name: this.$i18n.locale + '-category-details',
        params: { catName: transformedName }
      })
    }
  }
})
